/**
 * General VirtualizedListView tests
 */
import { useRef, useState } from 'preact/hooks';

import { DataState, ListItemContext, Range } from '../../UNSAFE_Collection';
import { Flex } from '../../UNSAFE_Flex';
import { Keys } from '../../utils/UNSAFE_keys';
import { VirtualizedListView } from '..';

import { setupIntersectionObserverMock } from '../../../test/mocks/mockIntersectionObserver';
import * as ListTests from '../../PRIVATE_List/__tests__/shared/listViewTests';
import { testSelection } from '../../PRIVATE_List/__tests__/shared/listViewSelectionTests';

type Employee<K> = {
  id: K;
  name: string;
};

type Props<K, D> = ListTests.Props<K, D>;

function TestComponent<K extends string | number>({
  currentItemVariant,
  data,
  initialKey,
  gridlines,
  onItemAction,
  selectedKeys,
  selectionMode
}: Props<K, Employee<K>>) {
  const rootRef = useRef(null);
  const [currentKey, setCurrentKey] = useState<K | undefined>(initialKey);
  const [selection, setSelection] = useState<Keys<K> | undefined>(selectedKeys);
  const [dataState, setDataState] = useState<DataState<K, Employee<K>>>({
    offset: 0,
    data: data.slice(0, 25),
    totalSize: data.length,
    sizePrecision: 'exact'
  });

  const loadRange = (range: Range) => {
    const startIndex = range.offset;
    const endIndex = startIndex + range.count;
    const newDataState = {
      ...dataState,
      offset: startIndex,
      data: data.slice(startIndex, endIndex)
    };
    setDataState(newDataState);
  };

  const getConfig = () => {
    return {
      scroller: () => {
        return rootRef.current;
      }
    };
  };

  return (
    <div
      style="height: calc(10 * var(--oj-c-PRIVATE-DO-NOT-USE-core-spacing-5x));overflow: auto"
      ref={rootRef}>
      <VirtualizedListView
        accessibleSummary="Basic list"
        data={dataState}
        currentKey={currentKey}
        selectedKeys={selection}
        selectionMode={selectionMode}
        viewportConfig={getConfig()}
        onCurrentKeyChange={(detail) => setCurrentKey(detail.value)}
        onSelectionChange={(detail) => setSelection(detail.value)}
        onLoadRange={loadRange}
        gridlines={gridlines}
        onItemAction={onItemAction}
        currentItemVariant={currentItemVariant}>
        {(context: ListItemContext<K, Employee<K>>) => (
          <Flex>
            {context.selector && context.selector()}
            <div
              className="item"
              style={{
                height: 'calc(var(--oj-c-PRIVATE-DO-NOT-USE-core-spacing-5x))',
                padding: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-spacing-3x)'
              }}>
              {context.data.name}
            </div>
          </Flex>
        )}
      </VirtualizedListView>
    </div>
  );
}

describe('Test VirtualizedListView component', () => {
  beforeEach(() => {
    if (!window.IntersectionObserver) {
      setupIntersectionObserverMock();
    }
  });

  // bind shared tests
  ListTests.testRender.bind(this)(TestComponent);
  ListTests.testAria.bind(this)(TestComponent);
  ListTests.testKeys.bind(this)(TestComponent);
  ListTests.testGridlines.bind(this)(TestComponent);
  ListTests.testItemAction.bind(this)(TestComponent);

  // selection tests
  testSelection.bind(this)(TestComponent);
});
